import * as React from "react";
import { useMemo, useState } from "react";
import "buffer";

import {
	onCompletionStinger1,
	onTransitionInAudioOff,
	onTransitionOutAudioOn,
	onTransitionInPlay,
	onTransitionInSetOutput3,
	onTransitionInSetOutput2,
	onTransitionOutPause,
	onTransitionOutRestart,
	onTransitionOutSetOutput3,
	onTransitionOutSetOutput2,
	onTransitionInScriptStart,
} from "../../features/triggers/triggers";
import { FileLoader } from "../../features/file-loader/FileLoader";
import produce from "immer";
import { getPresetInputs, toXml } from "../../util/presetUtils";
import { Input, InputWithIndex } from "../../features/input/types";
import {
	getInputByName,
	getInputKey,
	getInputTitle,
	isAudioInput,
	isAudioOrVideoInput,
	isMaterialInput,
	isVideoInput,
	Material,
	parseMaterialFromInputTitle,
} from "../../features/input/utils";
import { Inputs } from "../../features/input/Input";
import { downloadFile, getPatchedFilename } from "../../util/file";
import { Preset } from "../../App";

export type MaterialInput = Material & InputWithIndex;

export type MaterialsMap = Record<number, MaterialInput[]>;

const DEFAULT_MAIN_LANGUAGE = "RUS";
const DEFAULT_SECONDARY_LANGUAGE = "ENG";
const TEAMSPEAK_MAIN = "Teamspeak RUS";
const TEAMSPEAK_SECONDARY = "Teamspeak ENG";
const ZOOM_MAIN = "Zoom RUS";
const ZOOM_SECONDARY = "Zoom ENG";
const ZOOM_ORIGINAL = "Zoom original";
const OBS_CLOUD_CUT_SCRIPT_NAME = "OBS cloud cut";

const setUpAutoGain = (preset: Preset, enabled: boolean): Preset => {
	const inputs = preset.XML.Input;

	inputs.filter(isAudioOrVideoInput).forEach((input: Input) => {
		input._attributes.AudioAGCEnabled = enabled ? "False" : "True";
	});

	return preset;
};

type ExistingPresetInputs = {
	tsMain: InputWithIndex;
	tsSecondary: InputWithIndex;
	zoomMain: InputWithIndex;
	zoomSecondary: InputWithIndex;
	zoomOriginal: InputWithIndex;
};

const PresetPatcher: React.FC = () => {
	const [cnt, setCnt] = useState(0);
	const [mainLanguage, setMainLanguage] = useState(DEFAULT_MAIN_LANGUAGE);
	const [secondaryLanguage, setSecondaryLanguage] = useState(DEFAULT_SECONDARY_LANGUAGE);
	// const [teamspeakMainName, setTeamspeakMainName] = useState(TEAMSPEAK_MAIN);
	// const [teamspeakSecondaryName, setTeamspeakSecondaryName] = useState(TEAMSPEAK_SECONDARY);
	const [preset, setPreset] = useState<Preset>(null);
	const [presetFilename, setPresetFilename] = useState("");
	const [globalAutogainEnabled, setGlobalAutogainEnabled] = useState(false);
	const [bindToExistingPreset, setBindToExistingPreset] = useState(false);
	const [existingPreset, setExistingPreset] = useState<Preset>(null);

	const inputs = getPresetInputs(preset);

	console.log("PRESET", preset);
	console.log("EXISTING PRESET", existingPreset);

	const existingPresetInputs: ExistingPresetInputs | null = useMemo(() => {
		if (existingPreset) {
			return {
				tsMain: getInputByName(existingPreset.XML.Input, TEAMSPEAK_MAIN),
				tsSecondary: getInputByName(existingPreset.XML.Input, TEAMSPEAK_SECONDARY),
				zoomMain: getInputByName(existingPreset.XML.Input, ZOOM_MAIN),
				zoomSecondary: getInputByName(existingPreset.XML.Input, ZOOM_SECONDARY),
				zoomOriginal: getInputByName(existingPreset.XML.Input, ZOOM_ORIGINAL),
			};
		} else {
			return null;
		}
	}, [existingPreset]);

	return (
		<div className="PresetPatcher">
			{!preset && (
				<FileLoader
					onPresetLoaded={(filename, preset: Preset) => {
						setPresetFilename(filename);
						setPreset(preset);
					}}
				/>
			)}

			{preset && (
				<div>
					<div className="buttons">
						<button
							className="button-sort"
							onClick={() => {
								const updatedPreset = processMaterials(preset, mainLanguage, secondaryLanguage, existingPresetInputs);
								setPreset(updatedPreset);
								setCnt(cnt + 1);
							}}
						>
							Process materials
						</button>

						<button
							className="button-set-up-auto-gain"
							onClick={() => {
								setUpAutoGain(preset, globalAutogainEnabled);
								setGlobalAutogainEnabled(!globalAutogainEnabled);
							}}
						>
							{globalAutogainEnabled ? "Disable" : "Enable"} auto gain on all inputs
						</button>

						<button
							className="button-download"
							style={{ marginLeft: "auto" }}
							onClick={() => downloadFile(getPatchedFilename(presetFilename), toXml(preset))}
						>
							Download preset
						</button>

						<button
							className="button-restart"
							onClick={() => {
								setPreset(null);
								setPresetFilename(null);
							}}
						>
							Restart
						</button>
					</div>

					<br />

					<div>
						<label htmlFor="main-language">Main language: </label>
						<input
							id="main-language"
							type="text"
							value={mainLanguage}
							onChange={(event) => setMainLanguage(event.target.value)}
							style={{ width: "1.75rem" }}
						/>

						<label htmlFor="secondary-language" style={{ marginLeft: "1rem" }}>
							Secondary language:{" "}
						</label>
						<input
							id="secondary-language"
							type="text"
							value={secondaryLanguage}
							onChange={(event) => setSecondaryLanguage(event.target.value)}
							style={{ width: "1.75rem" }}
						/>
					</div>

					<br />

					{/* <label htmlFor="main-teamspeak">Teamspeak main input name: </label>
					<input
						id="main-teamspeak"
						type="text"
						value={teamspeakMainName}
						onChange={(event) => setTeamspeakMainName(event.target.value)}
					/> */}

					{/* <br />

					<label htmlFor="secondary-teamspeak">Teamspeak secondary input name: </label>
					<input
						id="secondary-teamspeak"
						type="text"
						value={teamspeakSecondaryName}
						onChange={(event) => setTeamspeakSecondaryName(event.target.value)}
					/> */}

					<input
						type="checkbox"
						id="bind-to-existing"
						checked={bindToExistingPreset}
						onChange={() => setBindToExistingPreset(!bindToExistingPreset)}
					/>
					<label htmlFor="bind-to-existing">Bind triggers to existing preset</label>

					<br />
					<br />

					{bindToExistingPreset && (
						<>
							{!existingPreset && (
								<FileLoader
									onPresetLoaded={(_, existingPreset) => {
										setExistingPreset(existingPreset);
									}}
								/>
							)}
							{existingPreset && (
								<div>
									<div>Teamspeak RUS ... {existingPresetInputs.tsMain.inputIndex >= 0 ? "OK" : "NOT FOUND"}</div>
									<div>Teamspeak ENG ... {existingPresetInputs.tsSecondary.inputIndex >= 0 ? "OK" : "NOT FOUND"}</div>
									<div>Zoom RUS ... {existingPresetInputs.zoomMain.inputIndex >= 0 ? "OK" : "NOT FOUND"}</div>
									<div>Zoom ENG ... {existingPresetInputs.zoomSecondary.inputIndex >= 0 ? "OK" : "NOT FOUND"}</div>
									<div>Zoom original ... {existingPresetInputs.zoomOriginal.inputIndex >= 0 ? "OK" : "NOT FOUND"}</div>
								</div>
							)}
							<br />
							<br />
						</>
					)}

					<div>
						Inputs: <strong>{inputs.length}</strong>
					</div>
					<br />

					<Inputs
						inputs={inputs}
						onInputUpdated={(input, number) => {
							const updatedPreset = produce(preset, (draft: any) => {
								draft.XML.Input[number - 1] = input;
							});
							setPreset(updatedPreset);
						}}
					/>
				</div>
			)}
		</div>
	);
};

const parseMaterialArray = (
	materialArray: MaterialInput[],
	mainLanguage: string,
	secondaryLanguage: string
): {
	main: MaterialInput;
	secondary?: MaterialInput;
	other?: MaterialInput[];
} => {
	let main: MaterialInput;
	let secondary: MaterialInput;
	let other: MaterialInput[] = [];

	materialArray.forEach((material) => {
		if (material.language === mainLanguage) {
			main = material;
		} else if (material.language === secondaryLanguage) {
			secondary = material;
		} else {
			other.push(material);
		}
	});

	return { main, secondary, other };
};

export const buildMaterialsMap = (inputs: Input[]): { materialsMap: MaterialsMap; indexes: number[] } => {
	const materials: MaterialInput[] = inputs
		.map((input, inputIndex) => ({ input, inputIndex }))
		.filter(({ input }) => isMaterialInput(input))
		.map((inputFeatures): MaterialInput => {
			const inputTitle = getInputTitle(inputFeatures.input);
			const materialFeatures = parseMaterialFromInputTitle(inputTitle);

			return {
				...inputFeatures,
				...materialFeatures,
			};
		});

	// materials.sort((a, b) => {
	// 	if (a.materialNumber === b.materialNumber) {
	// 		return a.language.localeCompare(b.language);
	// 	}
	// 	return a.materialNumber - b.materialNumber;
	// });

	const materialsInputPositionIndexes = materials.map(({ inputIndex }) => inputIndex);
	const materialsMap: MaterialsMap = {};

	materials.forEach((material) => {
		const { materialNumber } = material;

		if (!Array.isArray(materialsMap[materialNumber])) materialsMap[materialNumber] = [];

		const existingMaterial = materialsMap[materialNumber].find(
			(existingMaterial) =>
				existingMaterial.materialNumber === material.materialNumber && existingMaterial.language === material.language
		);
		if (existingMaterial) {
			alert(
				`Material ${material.materialNumber} in language ${material.language} is duplicated!\nDuplicated inputs: ${
					existingMaterial.inputIndex + 1
				}, ${material.inputIndex + 1}\n\nPlease, fix your preset, otherwise the patching can broke the preset!`
			);
		}

		materialsMap[materialNumber].push(material);
	});

	// materials.forEach((material) => {});

	return {
		materialsMap,
		indexes: materialsInputPositionIndexes,
	};
};

const setMainInputBuses = (input: Input) => {
	input._attributes.BusMaster = "True";

	input._attributes.BusA = "False";
	input._attributes.BusB = "False";
	input._attributes.BusC = "False";
	input._attributes.BusD = "False";
	input._attributes.BusE = "False";
	input._attributes.BusF = "False";
	input._attributes.BusG = "False";
};

const setSecondaryInputBuses = (input: Input) => {
	input._attributes.BusD = "True";

	input._attributes.BusMaster = "False";
	input._attributes.BusA = "False";
	input._attributes.BusB = "False";
	input._attributes.BusC = "False";
	input._attributes.BusE = "False";
	input._attributes.BusF = "False";
	input._attributes.BusG = "False";
};

const setOriginalInputBuses = (input: Input) => {
	input._attributes.BusA = "True";
	input._attributes.BusC = "True";

	input._attributes.BusMaster = "False";
	input._attributes.BusB = "False";
	input._attributes.BusD = "False";
	input._attributes.BusE = "False";
	input._attributes.BusF = "False";
	input._attributes.BusG = "False";
};

const setColorCategory = (
	{
		main,
		secondary,
		other,
	}: {
		main: MaterialInput;
		secondary?: MaterialInput;
		other?: MaterialInput[];
	},
	isVmixSpeaker: boolean
) => {
	const YELLOW_CATEGORY = "3";
	const PURPLE_CATEGORY = "4";

	const colorCategory = isVmixSpeaker ? PURPLE_CATEGORY : YELLOW_CATEGORY;

	main.input._attributes.Category = colorCategory;
	if (secondary?.input) {
		secondary.input._attributes.Category = colorCategory;
	}
	if (other) {
		other.forEach((otherMaterial) => {
			if (otherMaterial?.input) {
				otherMaterial.input._attributes.Category = colorCategory;
			}
		});
	}
};

const setAutoRestart = ({
	main,
	secondary,
}: {
	main: MaterialInput;
	secondary?: MaterialInput;
	other?: MaterialInput[];
}) => {
	if (main.vMixSpeaker) {
		main.input._attributes.AutoRestart = "False";
	}
	if (secondary && secondary.vMixSpeaker) {
		secondary.input._attributes.AutoRestart = "False";
	}
};

const setMainMaterialAttributes = (main: MaterialInput) => {
	main.input._attributes.Collapsed = "False";
	main.input._attributes.Muted = "False";
	setMainInputBuses(main.input);
};

const setSecondaryMaterialAttributes = (secondary: MaterialInput) => {
	secondary.input._attributes.Muted = "False";
	secondary.input._attributes.AutoAudioMixing = "False";
	secondary.input._attributes.Collapsed = "True";
	setSecondaryInputBuses(secondary.input);
};

const setOriginalMaterialAttributes = (original: MaterialInput) => {
	original.input._attributes.Muted = "False";
	original.input._attributes.AutoAudioMixing = "False";
	original.input._attributes.Collapsed = "True";
	setOriginalInputBuses(original.input);
};

export const processMaterials = (
	preset: Preset,
	mainLanguage: string,
	secondaryLanguage: string,
	existingPresetInputs: ExistingPresetInputs | null
) => {
	const inputs = preset.XML.Input;
	const { materialsMap, indexes } = buildMaterialsMap(inputs);

	const teamSpeak1 = existingPresetInputs?.tsMain || getInputByName(inputs, TEAMSPEAK_MAIN);
	const teamSpeak2 = existingPresetInputs?.tsSecondary || getInputByName(inputs, TEAMSPEAK_SECONDARY);

	const zoom1 = existingPresetInputs?.zoomMain || getInputByName(inputs, ZOOM_MAIN);
	const zoom2 = existingPresetInputs?.zoomSecondary || getInputByName(inputs, ZOOM_SECONDARY);
	const zoomOriginal = existingPresetInputs?.zoomOriginal || getInputByName(inputs, ZOOM_ORIGINAL);

	const errorMessages = [];
	if (!teamSpeak1.input) errorMessages.push(`${TEAMSPEAK_MAIN} is missing.`);
	if (!teamSpeak2.input) errorMessages.push(`${TEAMSPEAK_SECONDARY} is missing.`);
	if (!zoom1.input) errorMessages.push(`${ZOOM_MAIN} is missing.`);
	if (!zoom2.input) errorMessages.push(`${ZOOM_SECONDARY} is missing.`);
	if (!zoomOriginal.input) errorMessages.push(`${ZOOM_ORIGINAL} is missing.`);
	if (errorMessages.length > 0) {
		alert(errorMessages.join("\n"));
		return preset;
	}

	Object.keys(materialsMap).forEach((m) => {
		const inputArray: MaterialInput[] = materialsMap[m];
		const materials = parseMaterialArray(inputArray, mainLanguage, secondaryLanguage);

		const { main } = materials;
		setColorCategory(materials, main.vMixSpeaker);
		setAutoRestart(materials);
		setMainMaterialAttributes(main);

		if (inputArray.length === 1) {
			if (main.vMixSpeaker) {
				main.input._attributes.Triggers.ArrayOfInputTrigger.InputTrigger = [
					onTransitionInScriptStart(OBS_CLOUD_CUT_SCRIPT_NAME),
				];
			} else {
				main.input._attributes.Triggers.ArrayOfInputTrigger.InputTrigger = [
					onCompletionStinger1(),
					onTransitionInAudioOff(teamSpeak1),
					onTransitionInAudioOff(teamSpeak2),
					onTransitionInAudioOff(zoom1),
					onTransitionInAudioOff(zoom2),
					onTransitionInAudioOff(zoomOriginal),
					onTransitionOutAudioOn(teamSpeak1),
					onTransitionOutAudioOn(teamSpeak2),
					onTransitionOutAudioOn(zoom1),
					onTransitionOutAudioOn(zoom2),
					onTransitionOutAudioOn(zoomOriginal),
				];
			}

			preset.XML.Input[indexes.shift()] = main.input;
		} else if (inputArray.length === 2) {
			const { main, secondary } = materials;

			if (main.vMixSpeaker) {
				main.input._attributes.Triggers.ArrayOfInputTrigger.InputTrigger = [
					onTransitionInScriptStart(OBS_CLOUD_CUT_SCRIPT_NAME),
				];
			} else {
				main.input._attributes.Triggers.ArrayOfInputTrigger.InputTrigger = [
					onCompletionStinger1(),
					onTransitionInAudioOff(teamSpeak1),
					onTransitionInAudioOff(teamSpeak2),
					onTransitionInAudioOff(zoom1),
					onTransitionInAudioOff(zoom2),
					onTransitionInAudioOff(zoomOriginal),
					onTransitionOutAudioOn(teamSpeak1),
					onTransitionOutAudioOn(teamSpeak2),
					onTransitionOutAudioOn(zoom1),
					onTransitionOutAudioOn(zoom2),
					onTransitionOutAudioOn(zoomOriginal),
				];
			}

			if (isAudioInput(secondary.input)) {
				main.input._attributes.Overlay0 = getInputKey(secondary.input);
			} else if (isVideoInput(secondary.input)) {
				// TODO: Maybe handle vMix speaker differently?
				main.input._attributes.Triggers.ArrayOfInputTrigger.InputTrigger.push(
					...[
						onTransitionInPlay(secondary),
						onTransitionInSetOutput3(secondary),
						onTransitionInSetOutput2(secondary),
						onTransitionOutPause(secondary),
						onTransitionOutRestart(secondary),
						onTransitionOutSetOutput3("Active"),
						onTransitionOutSetOutput2("Active"),
					]
				);
			}

			preset.XML.Input[indexes.shift()] = main.input;
			preset.XML.Input[indexes.shift()] = secondary.input;

			setSecondaryMaterialAttributes(secondary);
		} else if (inputArray.length === 3) {
			const { main, secondary, other } = materials;

			if (main.vMixSpeaker) {
				main.input._attributes.Triggers.ArrayOfInputTrigger.InputTrigger = [
					onTransitionInScriptStart(OBS_CLOUD_CUT_SCRIPT_NAME),
				];
			} else {
				main.input._attributes.Triggers.ArrayOfInputTrigger.InputTrigger = [
					onCompletionStinger1(),
					onTransitionInAudioOff(teamSpeak1),
					onTransitionInAudioOff(teamSpeak2),
					onTransitionInAudioOff(zoom1),
					onTransitionInAudioOff(zoom2),
					onTransitionInAudioOff(zoomOriginal),
					onTransitionOutAudioOn(teamSpeak1),
					onTransitionOutAudioOn(teamSpeak2),
					onTransitionOutAudioOn(zoom1),
					onTransitionOutAudioOn(zoom2),
					onTransitionOutAudioOn(zoomOriginal),
				];
			}

			if (isAudioInput(secondary.input)) {
				main.input._attributes.Overlay0 = getInputKey(secondary.input);
			} else if (isVideoInput(secondary.input)) {
				main.input._attributes.Triggers.ArrayOfInputTrigger.InputTrigger.push(
					...[
						onTransitionInPlay(secondary),
						onTransitionInSetOutput3(secondary),
						onTransitionInSetOutput2(secondary),
						onTransitionOutPause(secondary),
						onTransitionOutRestart(secondary),
						onTransitionOutSetOutput3("Active"),
						onTransitionOutSetOutput2("Active"),
					]
				);
			}

			other.forEach((material, index) => {
				if (isAudioInput(material.input)) {
					main.input._attributes[`Overlay${index + 1}`] = getInputKey(material.input);
				}
			});

			preset.XML.Input[indexes.shift()] = main.input;
			preset.XML.Input[indexes.shift()] = secondary.input;
			other.forEach((material) => {
				preset.XML.Input[indexes.shift()] = material.input;
			});

			setSecondaryMaterialAttributes(secondary);

			other.forEach((material) => {
				if (isAudioInput(material.input)) {
					setOriginalMaterialAttributes(material);
				}
			});
		}
	});

	return preset;
};

export default PresetPatcher;
