import { decode, encode } from "html-entities";
import { json2xml, xml2js } from "xml-js";

export const getPresetInputs = (preset: any) => {
	return preset?.XML?.Input || [];
};

export const parsePreset = (presetFileContent: string) => {
	const presetUnpacked = xml2js(presetFileContent, { compact: true });
	const preset = unpackPreset(presetUnpacked);
	return preset;
};

export const toXml = (preset: any): string => {
	return packPreset(preset);
};

const parseXml = (obj: string) => xml2js(obj, { compact: true });

const buildXml = (obj: any, ignoreHtmlEntities = false) => {
	const xml = json2xml(JSON.stringify(obj), { compact: true });
	return ignoreHtmlEntities ? xml : encode(xml.replace(/\n/g, "\r\n"));
};

// const wrapXml = (str, wrapperElement = 'xml') =>
//   `<${wrapperElement}>${str}</${wrapperElement}>`

const unpackPreset = (preset: any) => {
	preset.XML.Input.forEach((input) => {
		[
			"XML",
			"Positions",
			"PositionsExtended",
			"VirtualPTZPosition",
			"Triggers",
			"PresetsXML",
			"CountdownXML",
			"DataSourcesXML",
		].forEach((inputAttribute) => {
			if (input._attributes[inputAttribute]) {
				input._attributes[inputAttribute] = parseXml(input._attributes[inputAttribute]);
			}
		});

		if (input._attributes.AudioRackXML) input._attributes.AudioRackXML = parseXml(input._attributes.AudioRackXML);
	});

	preset.XML.State._attributes.PlayLists = parseXml(preset.XML.State._attributes.PlayLists);
	if (preset.XML?.RecordingSettings?.FFMPEGFormatXML) {
		preset.XML.RecordingSettings.FFMPEGFormatXML._text = parseXml(
			decode(preset.XML.RecordingSettings.FFMPEGFormatXML._text)
		);
	}
	if (preset.XML?.RecordingSettings2?.FFMPEGFormatXML) {
		preset.XML.RecordingSettings2.FFMPEGFormatXML._text = parseXml(
			decode(preset.XML.RecordingSettings2.FFMPEGFormatXML._text)
		);
	}

	if (preset.XML?.MultiCorderSettings?.FFMPEGFormatXML) {
		preset.XML.MultiCorderSettings.FFMPEGFormatXML._text = parseXml(
			decode(preset.XML.MultiCorderSettings.FFMPEGFormatXML._text)
		);
	}

	const matrixPositions = Object.keys(preset.XML.OverlaySettings).filter((overlaySetting) =>
		overlaySetting.startsWith("MatrixPosition")
	);

	matrixPositions.forEach((matrixPositionName) => {
		const matrixPosition = preset.XML.OverlaySettings[matrixPositionName];
		matrixPosition._text = parseXml(decode(matrixPosition._text));
	});

	const audioBuses = Object.keys(preset.XML).filter((setting) => {
		return setting.startsWith("AudioBus") || setting === "AudioMaster";
	});

	audioBuses.forEach((busName) => {
		const bus = preset.XML[busName];
		bus._attributes.AudioRackXML = parseXml(bus._attributes.AudioRackXML);
	});

	// preset.XML.StreamingSettings.StreamingSetting.forEach((streamingSetting) => {
	// ["0", "1", "2"].forEach((destinationNumber) => {
	// const destination = streamingSetting[`Destination${destinationNumber}`];
	// destination._text = parseXml(wrapXml(decode(destination._text)));
	// console.log("1", decode(destination._text));

	// ["Facebook", "Periscope_Twitter"].forEach((network) => {
	//   if (destination._text?.xml?.[`${network}.xml`])
	//     destination._text.xml[`${network}.xml`]._text = parseXml(wrapXml(destination._text.xml[`${network}.xml`]._text));
	//   if (destination._text?.xml?.[`${network}.settingsxml`])
	//     destination._text.xml[`${network}.settingsxml`]._text = parseXml(wrapXml(destination._text.xml[`${network}.settingsxml`]._text));
	// });
	// });
	// });

	return preset;
};

const packPreset = (presetObject: any) => {
	const preset = JSON.parse(JSON.stringify(presetObject));

	preset.XML.Input.forEach((input: any) => {
		if (input._attributes.Title) {
			input._attributes.Title = encode(input._attributes.Title);
		}
	});

	preset.XML.Input.forEach((input: any) => {
		[
			"XML",
			"Positions",
			"PositionsExtended",
			"VirtualPTZPosition",
			"Triggers",
			"PresetsXML",
			"CountdownXML",
			"DataSourcesXML",
		].forEach((inputAttribute) => {
			if (input._attributes[inputAttribute]) {
				input._attributes[inputAttribute] = buildXml(input._attributes[inputAttribute]);
			}
		});

		if (input._attributes.AudioRackXML) input._attributes.AudioRackXML = buildXml(input._attributes.AudioRackXML);
	});

	preset.XML.State._attributes.PlayLists = buildXml(preset.XML.State._attributes.PlayLists);
	["RecordingSettings", "RecordingSettings2", "MultiCorderSettings"].forEach((recordingSetup) => {
		if (preset.XML[recordingSetup]?.FFMPEGFormatXML) {
			preset.XML[recordingSetup].FFMPEGFormatXML._text = buildXml(preset.XML[recordingSetup].FFMPEGFormatXML._text);
		}
	});

	const matrixPositions = Object.keys(preset.XML.OverlaySettings).filter((overlaySetting) =>
		overlaySetting.startsWith("MatrixPosition")
	);

	matrixPositions.forEach((matrixPositionName) => {
		const matrixPosition = preset.XML.OverlaySettings[matrixPositionName];
		matrixPosition._text = buildXml(matrixPosition._text);
	});

	const audioBuses = Object.keys(preset.XML).filter((setting) => {
		return setting.startsWith("AudioBus") || setting === "AudioMaster";
	});

	audioBuses.forEach((busName) => {
		const bus = preset.XML[busName];
		bus._attributes.AudioRackXML = buildXml(bus._attributes.AudioRackXML);
	});

	// preset.XML.StreamingSettings.StreamingSetting.forEach(streamingSetting => {
	//   ;['0', '1', '2'].forEach(destinationNumber => {
	//     const destination = streamingSetting[`Destination${destinationNumber}`]
	//     destination._text = buildXml(wrapXml(destination._text))
	//     ;['Facebook', 'Periscope_Twitter'].forEach(network => {
	//       if (destination._text?.xml?.[`${network}.xml`])
	//         destination._text.xml[`${network}.xml`]._text = buildXml(
	//           wrapXml(destination._text.xml[`${network}.xml`]._text)
	//         )
	//       if (destination._text?.xml?.[`${network}.settingsxml`])
	//         destination._text.xml[`${network}.settingsxml`]._text = buildXml(
	//           wrapXml(destination._text.xml[`${network}.settingsxml`]._text)
	//         )
	//     })
	//   })
	// })

	return buildXml(preset, true);
};
