import { Trigger, TriggerType } from "./types";
import { VmixFunction } from "../types";
import { generateId } from "../../util/generateId";
import { InputWithIndex } from "../input/types";
import { getInputKey } from "../input/utils";

const DEFAULT_DURATION = 500;

type PlaceholderInput = "Active" | "Dynamic1" | "Dynamic2" | "Dynamic3" | "Dynamic4" | "Preview";
type TargetInput = InputWithIndex | PlaceholderInput;

const PlaceholderInputNumbers: Record<PlaceholderInput, number> = {
	Preview: 0,
	Active: -1,
	Dynamic1: -4,
	Dynamic2: -5,
	Dynamic3: -6,
	Dynamic4: -7,
};

type TriggerOptions = {
	duration?: number;
	useNumberInsteadOfIndex?: boolean;
	value?: string;
};

export const onTransitionInPlay = (targetInput: TargetInput): Trigger => {
	return buildTrigger(TriggerType.OnTransitionIn, VmixFunction.Play, targetInput, { useNumberInsteadOfIndex: true });
};

export const onTransitionInRestart = (targetInput: TargetInput): Trigger => {
	return buildTrigger(TriggerType.OnTransitionIn, VmixFunction.Restart, targetInput, { useNumberInsteadOfIndex: true });
};

export const onTransitionInSetOutput2 = (targetInput: TargetInput): Trigger => {
	return buildTrigger(TriggerType.OnTransitionIn, VmixFunction.SetOutput2, targetInput, {
		useNumberInsteadOfIndex: true,
	});
};

export const onTransitionOutPause = (targetInput: TargetInput): Trigger => {
	return buildTrigger(TriggerType.OnTransitionOut, VmixFunction.Pause, targetInput, { useNumberInsteadOfIndex: true });
};

export const onTransitionOutRestart = (targetInput: TargetInput): Trigger => {
	return buildTrigger(TriggerType.OnTransitionOut, VmixFunction.Restart, targetInput, {
		useNumberInsteadOfIndex: true,
	});
};

export const onTransitionOutSetOutput3 = (targetInput: TargetInput): Trigger => {
	return buildTrigger(TriggerType.OnTransitionOut, VmixFunction.SetOutput3, targetInput, {
		useNumberInsteadOfIndex: true,
	});
};

export const onTransitionOutSetOutput2 = (targetInput: TargetInput): Trigger => {
	return buildTrigger(TriggerType.OnTransitionOut, VmixFunction.SetOutput2, targetInput, {
		useNumberInsteadOfIndex: true,
	});
};

export const onTransitionInSetOutput3 = (targetInput: TargetInput): Trigger => {
	return buildTrigger(TriggerType.OnTransitionIn, VmixFunction.SetOutput3, targetInput, {
		useNumberInsteadOfIndex: true,
	});
};

export const onTransitionInOverlayInput1In = (targetInput: TargetInput): Trigger => {
	return buildTrigger(TriggerType.OnTransitionIn, VmixFunction.OverlayInput1In, targetInput, {
		useNumberInsteadOfIndex: true,
	});
};

export const onTransitionOutOverlayInput1Off = (): Trigger => {
	return buildTrigger(TriggerType.OnTransitionOut, VmixFunction.OverlayInput1Off);
};

export const onTransitionInAudioOff = (targetInput: TargetInput): Trigger => {
	return buildTrigger(TriggerType.OnTransitionIn, VmixFunction.AudioOff, targetInput);
};

export const onTransitionInScriptStart = (scriptName: string): Trigger => {
	return buildTrigger(TriggerType.OnTransitionIn, VmixFunction.ScriptStart, undefined, { value: scriptName });
};

export const onTransitionOutAudioOn = (targetInput: TargetInput): Trigger => {
	return buildTrigger(TriggerType.OnTransitionOut, VmixFunction.AudioOn, targetInput);
};

export const onOverlayInAudioInputOn = (targetInput: TargetInput): Trigger => {
	return buildTrigger(TriggerType.OnOverlayIn, VmixFunction.AudioOn, targetInput);
};

export const onOverlayOutAudioInputOff = (targetInput: TargetInput): Trigger => {
	return buildTrigger(TriggerType.OnOverlayOut, VmixFunction.AudioOff, targetInput);
};

export const onCompletionStinger1 = (): Trigger => {
	return buildTrigger(TriggerType.OnCompletion, VmixFunction.Stinger1);
};

export const onOverlayInOverlayInput2In = (targetInput: TargetInput): Trigger => {
	return buildTrigger(TriggerType.OnOverlayIn, VmixFunction.OverlayInput2In, targetInput);
};

export const onOverlayOutOverlayInput2Off = (): Trigger => {
	return buildTrigger(TriggerType.OnOverlayOut, VmixFunction.OverlayInput2Off);
};

const buildTrigger = (
	trigger: TriggerType,
	func: VmixFunction,
	targetInput?: TargetInput,
	options?: TriggerOptions
): Trigger => {
	const { duration = DEFAULT_DURATION, useNumberInsteadOfIndex = false, value } = options || {};

	let targetInputId: string;
	let targetInputNumber = 0;

	if (targetInput && typeof targetInput === "object") {
		targetInputId = getInputKey(targetInput.input);
		targetInputNumber = targetInput.inputIndex + (useNumberInsteadOfIndex ? 1 : 0);
	} else if (typeof targetInput === "string") {
		targetInputId = "00000000-0000-0000-0000-000000000000";
		targetInputNumber = PlaceholderInputNumbers[targetInput];
	}

	return {
		DisplayType: {
			_text: "Default",
		},
		ID: {
			_text: generateId(),
		},
		GetInputByNumber: {
			_text: false,
		},
		Disabled: {
			_text: false,
		},
		ControllerHidden: {
			_text: false,
		},
		Value: value ? { _text: value } : {},
		SelectedIndex: {
			_text: -1,
		},
		ExpandedKey: {
			_text: "None",
		},
		Key: {
			_text: "None",
		},
		Function: {
			_text: func,
		},
		Duration: {
			_text: duration,
		},
		Input: {
			Key: {
				_text: targetInputId,
			},
			Number: {
				_text: targetInputNumber,
			},
		},
		Mix: {
			_text: 0,
		},
		ReplayChannel: {
			_text: "Current",
		},
		MIDIChannel: {
			_text: 0,
		},
		MIDINote: {
			_text: 0,
		},
		MIDIValue: {
			_text: 0,
		},
		MIDIValueEnabled: {
			_text: false,
		},
		MIDIValueType: {
			_text: "Default",
		},
		JSIndex: {
			_text: 0,
		},
		JSButton: {
			_text: 0,
		},
		JSPressureEnabled: {
			_text: false,
		},
		Delay: {
			_text: 0,
		},
		Trigger: {
			_text: trigger,
		},
	};
};
