import * as React from "react";
import { useState } from "react";
import produce from "immer";
import { Preset } from "../../App";
import { FolderLoader } from "../../features/file-loader/FolderLoader";
import { Inputs } from "../../features/input/Input";
import { Bus } from "../../features/input/types";
import { downloadFile } from "../../util/file";
import { getPresetInputs, toXml } from "../../util/presetUtils";
import { buildMaterialsMap, MaterialInput, MaterialsMap } from "../PresetPatcher/PresetPatcher";
import { getInputKey, isAudioInput } from "../../features/input/utils";
import { buildColourBarsInput } from "../../features/input/inputs";

const MAX_VMIX_MATERIAL_LANGUAGES = 8;
const DEFAULT_MAIN_LANGUAGE = "EN";

const getAllMaterialLanguages = (materialsMap: MaterialsMap): string[] => {
	const allMaterials = Object.values(materialsMap).flat();
	return [...new Set(allMaterials.map((material) => material.language))].sort();
};

const getBusByIndex = (index: number): Bus => {
	return Object.values(Bus)?.[index] as Bus;
};

const processMaterials = (preset: Preset, mainLanguage: string) => {
	const inputs = preset.XML.Input;
	const { materialsMap } = buildMaterialsMap(inputs);

	const allLanguages = getAllMaterialLanguages(materialsMap);

	if (!allLanguages.includes(mainLanguage)) {
		alert(`There are no materials of language '${mainLanguage}'`);
		return preset;
	}

	if (allLanguages.length > MAX_VMIX_MATERIAL_LANGUAGES) {
		throw new Error(`Invalid number of languages. Maximum is ${MAX_VMIX_MATERIAL_LANGUAGES}.`);
	}

	let i = 0;

	Object.keys(materialsMap).forEach((materialNumber) => {
		// for each video number (02, 04, 06)
		const materialArray: MaterialInput[] = materialsMap[materialNumber];

		// loop for all languages mapped to buses
		allLanguages.forEach((language, index) => {
			// if looped language is container in currently processed video number
			const materialIndex = materialArray.findIndex((materialInput) => materialInput.language === language);
			if (materialIndex >= 0) {
				const materialInput = materialArray[materialIndex];

				preset.XML.Input[i++] = materialInput.input;

				const bus = getBusByIndex(index);
				Object.values(Bus).forEach((iteratedBus) => {
					if (iteratedBus === bus) {
						materialInput.input._attributes[iteratedBus] = "True";
					} else {
						materialInput.input._attributes[iteratedBus] = "False";
					}
				});

				if (language === mainLanguage) {
					materialArray
						.filter((iteratedMaterialInput) => isAudioInput(iteratedMaterialInput.input))
						.forEach((iteratedMaterialInput, index1) => {
							materialInput.input._attributes[`Overlay${index1 + 1}`] = getInputKey(iteratedMaterialInput.input);
						});
				}
			}
		});
	});

	preset.XML.Input[i] = buildColourBarsInput();
	preset.XML.State._attributes.Main = i;

	return preset;
};

const LanguagePresetBuilder: React.FC = () => {
	const [cnt, setCnt] = useState(0);
	const [mainLanguage, setMainLanguage] = useState(DEFAULT_MAIN_LANGUAGE);
	const [preset, setPreset] = useState<Preset>(null);

	const inputs = getPresetInputs(preset);

	return (
		<div className="LanguagePresetBuilder">
			{preset === null && (
				<FolderLoader
					onPresetLoaded={(preset) => {
						setPreset(preset);
					}}
				/>
			)}

			{preset && (
				<div>
					<div className="buttons">
						<button
							className="button-sort"
							onClick={() => {
								const updatedPreset = processMaterials(preset, mainLanguage);
								setPreset(updatedPreset);
								setCnt(cnt + 1);
							}}
						>
							Process materials
						</button>

						<button
							className="button-download"
							style={{ marginLeft: "auto" }}
							onClick={() => {
								const filename =
									getAllMaterialLanguages(buildMaterialsMap(preset.XML.Input).materialsMap)
										.toString()
										.replace(/,/g, "_") + ".vmix";
								downloadFile(filename, toXml(preset));
							}}
						>
							Download preset
						</button>

						<button
							className="button-restart"
							onClick={() => {
								setPreset(null);
								setMainLanguage(DEFAULT_MAIN_LANGUAGE);
							}}
						>
							Restart
						</button>
					</div>

					<label htmlFor="main-language">Main language: </label>
					<input
						id="main-language"
						type="text"
						value={mainLanguage}
						onChange={(event) => setMainLanguage(event.target.value)}
					/>

					<br />
					<br />

					<Inputs
						inputs={inputs}
						onInputUpdated={(input, number) => {
							const updatedPreset = produce(preset, (draft: any) => {
								draft.XML.Input[number - 1] = input;
							});
							setPreset(updatedPreset);
						}}
						matrix={true}
					/>
				</div>
			)}
		</div>
	);
};

export default LanguagePresetBuilder;

