import { Input, InputType, InputWithIndex } from "./types";

export type Material = {
	language: string;
	materialNumber: number;
	vMixSpeaker: boolean;
};

const MATERIAL_INPUT_NAME_REGEX = /^([0-9]+)_(vs_)?([a-zA-Z]+).+\..+$/;
export const SLIDE_MATERIAL_INPUT_NAME_REGEX = /^([0-9]+).([0-9]+).+\..+$/;

export const isVideoInput = (input: Input): boolean => {
	const inputType = input._attributes.Type;
	return inputType === InputType.Video;
};

export const isAudioInput = (input: Input): boolean => {
	const inputType = input._attributes.Type;
	return inputType === InputType.Audio;
};

export const isImageInput = (input: Input): boolean => {
	const inputType = input._attributes.Type;
	return inputType === InputType.Image;
};

export const isAudioOrVideoInput = (input: Input): boolean => {
	return isAudioInput(input) || isVideoInput(input);
};

export const isMaterialInput = (input: Input): boolean => {
	return isAudioOrVideoInput(input) && Boolean(getInputTitle(input).match(MATERIAL_INPUT_NAME_REGEX));
};

export const getInputTitle = (input: Input): string => {
	return input._attributes.Title || input._attributes.OriginalTitle;
};

export const getInputKey = (input: Input): string => {
	return input._attributes.Key;
};

export const parseMaterialFromInputTitle = (inputTitle: string): Material => {
	const normalizedTitle = inputTitle.replace(/\s+/g, "_").replace(/_+/, "_");

	const matches = normalizedTitle.match(MATERIAL_INPUT_NAME_REGEX);
	return {
		language: matches[3],
		materialNumber: Number(matches[1]),
		vMixSpeaker: !!matches[2],
	};
};

export const getInputIcon = (input: Input): string => {
	const inputType = input._attributes.Type;

	switch (inputType) {
		case InputType.Video:
			return "📺";
		case InputType.Audio:
			return "🔈";
		case InputType.Photos:
			return "🎞";
		case InputType.Camera:
			return "🎥";
		case InputType.AudioInput:
			return "🎤";
		case InputType.Image:
			return "🖼";
		case InputType.Color:
			return "🎨";
		case InputType.NDI:
			return "🖥";
		case InputType.Title:
			return "💬";
		case InputType.List:
			return "🗳";
		case InputType.Blank:
			return "🗌";
		default:
			return `#${inputType}#`;
	}
};

export const getInputByName = (inputs: Input[], inputName: string): InputWithIndex => {
	const inputIndex = inputs.findIndex((input) => {
		return getInputTitle(input) === inputName;
	});

	return {
		input: inputs[inputIndex],
		inputIndex,
	};
};

export const getInputByKey = (inputs: Input[], inputKey: string): InputWithIndex => {
	const inputIndex = inputs.findIndex((input) => {
		return getInputKey(input) === inputKey;
	});

	return {
		input: inputs[inputIndex],
		inputIndex,
	};
};
