import * as React from "react";
import produce from "immer";
import { Input } from "./types";
import { getInputByKey, getInputIcon, getInputKey, getInputTitle, isAudioOrVideoInput, isImageInput } from "./utils";

const renderTriggers = (input: Input) => {
	const triggers = input?._attributes?.Triggers?.ArrayOfInputTrigger?.InputTrigger;

	let triggerArray = [];

	if (triggers) {
		if (Array.isArray(triggers)) {
			triggerArray.push(...triggers);
		} else {
			triggerArray.push(triggers);
		}
	}

	return <div className="input-triggers">Triggers: {triggerArray.length}</div>;
};

const renderBusCheckboxes: React.FC<InputProps> = ({ input, inputNumber, onInputUpdated, matrix }) => {
	const busAttributes = ["BusMaster", "BusA", "BusB", "BusC", "BusD", "BusE", "BusF", "BusG"];
	const busCheckboxClasses = matrix ? "bus-checkbox--large" : "";

	return (
		<div style={{ display: "flex" }}>
			{busAttributes.map((busAttribute) => {
				return (
					<div className="bus-checkbox" key={busAttribute}>
						<input
							type="checkbox"
							className={busCheckboxClasses}
							checked={input._attributes[busAttribute] === "True"}
							onChange={() => {
								const updatedInput = produce(input, (draft: any) => {
									const busStatus = input._attributes[busAttribute] === "True" ? "False" : "True";
									draft._attributes[busAttribute] = busStatus;
								});

								onInputUpdated(updatedInput, inputNumber);
							}}
						/>
						<label>{busAttribute[3]}</label>
					</div>
				);
			})}
		</div>
	);
};

const renderLayers: React.FC<InputProps> = (props) => {
	const overlays = Object.keys(props.input._attributes).filter((attr) => attr.startsWith("Overlay"));

	if (overlays.length === 0) return <div />;

	const layers = overlays.map((overlayNumber) => {
		const overlayInputKey: string = props.input._attributes[overlayNumber];
		const { input, inputIndex } = getInputByKey(props.inputs, overlayInputKey);
		return (
			<div style={{ display: "flex" }} key={getInputKey(input) + overlayNumber}>
				<div className="">({overlayNumber[7]})&nbsp;&nbsp;</div>
				<div
					className="input-number"
					onClick={() => {
						const inputBlock = document.querySelector(`#input-${input._attributes.Key}`);
						inputBlock?.scrollIntoView({ behavior: "smooth" });
					}}
				>
					{inputIndex + 1}
				</div>
				<div>{getInputTitle(input)}</div>
			</div>
		);
	});

	return (
		<div className="input-layers">
			<span>Layers:</span>
			<div className="input-layers-inputs">{layers}</div>
		</div>
	);
};

type InputProps = {
	input: Input;
	inputNumber: number;
	inputs: Input[];
	onInputUpdated: InputUpdatedFn;
	matrix?: boolean;
	className?: string;
	hideTriggers?: boolean;
};

export const SingleInput: React.FC<InputProps> = (props: InputProps) => {
	const { input, inputNumber, onInputUpdated, matrix, className, hideTriggers } = props;

	const autoGainInputId = input._attributes.Key + "_autogain";
	const autoMixInputId = input._attributes.Key + "_automix";
	const audioInputId = input._attributes.Key + "_audio";

	return (
		<div className={`input ${className}`} key={input._attributes.Key} id={`input-${input._attributes.Key}`}>
			<div className="input-number">{inputNumber}</div>
			<div>
				<div>
					<span className="input-icon">{getInputIcon(input)}</span>
					<span style={{ marginLeft: "4px" }}>{getInputTitle(input)}</span>
				</div>

				{!matrix && <div className="input-key">{getInputKey(input)}</div>}

				{renderLayers(props)}
			</div>

			<br />

			<div className="input-features">
				{(isAudioOrVideoInput(input) || isImageInput(input)) && (
					<div>
						{!hideTriggers && renderTriggers(input)}

						{!isImageInput(input) && (
							<>
								<label htmlFor={audioInputId}>Audio: </label>
								<input
									type="checkbox"
									id={audioInputId}
									checked={input._attributes.Muted === "False"}
									onChange={() => {
										const updatedInput = produce(input, (draft: any) => {
											const muted = input._attributes.Muted === "True" ? "False" : "True";

											draft._attributes.Muted = muted;
										});

										onInputUpdated(updatedInput, inputNumber);
									}}
								/>

								<br />

								<label htmlFor={autoGainInputId}>Auto Gain: </label>
								<input
									type="checkbox"
									id={autoGainInputId}
									checked={input._attributes.AudioAGCEnabled === "True"}
									onChange={() => {
										const updatedInput = produce(input, (draft: any) => {
											const audioAGCEnabled = input._attributes.AudioAGCEnabled === "True" ? "False" : "True";

											draft._attributes.AudioAGCEnabled = audioAGCEnabled;
										});

										onInputUpdated(updatedInput, inputNumber);
									}}
								/>

								<br />

								<label htmlFor={autoMixInputId}>Auto Mix: </label>
								<input
									type="checkbox"
									id={autoMixInputId}
									checked={input._attributes.AutoAudioMixing === "True"}
									onChange={() => {
										const updatedInput = produce(input, (draft: any) => {
											const autoAudioMixing = input._attributes.AutoAudioMixing === "True" ? "False" : "True";

											draft._attributes.AutoAudioMixing = autoAudioMixing;
										});

										onInputUpdated(updatedInput, inputNumber);
									}}
								/>
							</>
						)}

						{!isImageInput(input) && (
							<>
								<br />
								{renderBusCheckboxes(props)}
							</>
						)}
					</div>
				)}
			</div>
		</div>
	);
};

type InputUpdatedFn = (input: Input, inputNumber: number) => void;

type InputsProps = {
	inputs: Input[];
	onInputUpdated: InputUpdatedFn;
	matrix?: boolean;
	shiftedInputKeys?: string[];
	hideTriggers?: boolean;
};

export const Inputs = ({ inputs, onInputUpdated, matrix, shiftedInputKeys, hideTriggers }: InputsProps) => {
	if (!inputs) {
		return <EmptyInputs />;
	}

	return (
		<div>
			{inputs.map((input, index) => {
				const isShifted = shiftedInputKeys?.includes(getInputKey(input));
				const classes = isShifted ? "single-input--shifted" : "";

				return (
					<SingleInput
						key={getInputKey(input)}
						input={input}
						inputs={inputs}
						inputNumber={index + 1}
						onInputUpdated={onInputUpdated}
						matrix={matrix}
						className={classes}
						hideTriggers={hideTriggers}
					/>
				);
			})}
		</div>
	);
};

export const EmptyInputs = () => {
	return <div>No inputs...</div>;
};
