import { TextNode, VmixFunction } from "../types";

export enum TriggerType {
	OnTransitionIn = "OnTransitionIn",
	OnTransitionOut = "OnTransitionOut",
	OnOverlayIn = "OnOverlayIn",
	OnOverlayOut = "OnOverlayOut",
	OnCompletion = "OnCompletion",
}

export type Trigger = {
	DisplayType: TextNode;
	ID: TextNode;
	GetInputByNumber: TextNode<boolean>;
	Disabled: TextNode<boolean>;
	ControllerHidden: TextNode<boolean>;
	Value: Record<string, unknown>;
	SelectedIndex: TextNode<number>;
	ExpandedKey: TextNode;
	Key: TextNode;
	Function: TextNode<VmixFunction>;
	Duration: TextNode<number>;
	Input: {
		Key: TextNode;
		Number: TextNode<number>;
	};
	Mix: TextNode<0 | 1 | 2 | 3>;
	ReplayChannel: TextNode;
	MIDIChannel: TextNode<number>;
	MIDINote: TextNode<number>;
	MIDIValue: TextNode<number>;
	MIDIValueEnabled: TextNode<boolean>;
	MIDIValueType: TextNode;
	JSIndex: TextNode<number>;
	JSButton: TextNode<number>;
	JSPressureEnabled: TextNode<boolean>;
	Delay: TextNode<number>;
	Trigger: TextNode<TriggerType>;
};
