import { generateId } from "../../util/generateId";
import { BoolType } from "../types";
import { Bus, InputType, InputWithText } from "./types";

const XML_DECLARATION = {
	_declaration: {
		_attributes: {
			version: "1.0",
			encoding: "utf-16",
		},
	},
};

const XML_SCHEMA = {
	_attributes: {
		"xmlns:xsi": "http://www.w3.org/2001/XMLSchema-instance",
		"xmlns:xsd": "http://www.w3.org/2001/XMLSchema",
	},
};

const EMPTY_POSITION = {
	Hidden: {
		_text: "false",
	},
	Border: {
		Enabled: {
			_text: "false",
		},
		Thickness: {
			_text: "0",
		},
		Radius: {
			_text: "0",
		},
		ColourHTML: {
			_text: "White",
		},
	},
	Mirror: {
		_text: "false",
	},
	ZoomX: {
		_text: "1",
	},
	ZoomY: {
		_text: "1",
	},
	PostZoomX: {
		_text: "1",
	},
	PostZoomY: {
		_text: "1",
	},
	RotateOrigin: {
		X: {
			_text: "0",
		},
		Y: {
			_text: "0",
		},
		Z: {
			_text: "0",
		},
	},
	Rotate: {
		X: {
			_text: "0",
		},
		Y: {
			_text: "0",
		},
		Z: {
			_text: "0",
		},
	},
	PanX: {
		_text: "0",
	},
	PanY: {
		_text: "0",
	},
};

const EMPTY_WITH_MULTIPLY_POSITION = {
	...EMPTY_POSITION,
	MultiplyPosition: EMPTY_POSITION,
};

const PTZ_OPTIONS = {
	PTZProvider: "",
	PTZConnection: "",
	PTZAutoConnect: "False",
	PTZDefaultPanTiltSpeed: "0.5",
	PTZDefaultZoomSpeed: "0.5",
	PTZDefaultPositionSpeed: "1",
	PTZDefaultFocusSpeed: "0.5",
	PTZDefaultFocusEnabled: "False",
	PTZDefaultTallyEnabled: "False",
	PTZAlwaysShowThumbnail: "False",
};

const VIDEOSHADER_OPTIONS = {
	VideoShader_ColorCorrectionSourceEnabled: "0",
	VideoShader_White: "1",
	VideoShader_Black: "0",
	VideoShader_Red: "0",
	VideoShader_Green: "0",
	VideoShader_Blue: "0",
	VideoShader_Alpha: "1",
	VideoShader_Saturation: "1",
	VideoShader_CCLiftR: "0",
	VideoShader_CCLiftG: "0",
	VideoShader_CCLiftB: "0",
	VideoShader_CCGammaR: "1",
	VideoShader_CCGammaG: "1",
	VideoShader_CCGammaB: "1",
	VideoShader_CCGainR: "1",
	VideoShader_CCGainG: "1",
	VideoShader_CCGainB: "1",
	VideoShader_Saturation2: "0",
	VideoShader_Hue: "0",
	VideoShader_Rec601Fix: "False",
	VideoShader_ColorKey: "0",
	VideoShader_Deinterlace: "False",
	VideoShader_Sharpen: "False",
	VideoShader_ToleranceRed: "0",
	VideoShader_ToleranceGreen: "0",
	VideoShader_ToleranceBlue: "0",
	VideoShader_ToleranceGreenGap: "0",
	VideoShader_GreenFilter: "False",
	VideoShader_GreenFilterTransparencyThreshold: "1",
	VideoShader_LumaKeyThreshold: "0",
	VideoShader_AntiAliasing: "False",
	VideoShader_AntiAliasingFilter: "0",
	VideoShader_ClippingX1: "0",
	VideoShader_ClippingX2: "1",
	VideoShader_ClippingY1: "0",
	VideoShader_ClippingY2: "1",
	VideoShader_PremultipliedAlpha: "False",
};

const buildInput = (
	type: InputType,
	originalTitle: string,
	filePath: string,
	collapsed: BoolType,
	buses: Partial<Record<Bus, BoolType>>,
	overlayInputKeys?: string[]
): InputWithText => {
	const overlays = overlayInputKeys?.reduce((obj, inputKey, index) => {
		return { ...obj, [`Overlay${index}`]: inputKey };
	}, {});

	return {
		_attributes: {
			Type: type,
			Position: "0",
			RangeStart: "0",
			RangeStop: "0",
			State: "1",
			OriginalTitle: originalTitle,
			ShortcutMappings: "",
			Key: generateId(),
			Loop: "False",
			VolumeF: "1",
			Muted: "True",
			BalanceF: "0",
			AspectRatio: "100",
			Category: "0",
			MouseClickAction: "0",
			Collapsed: collapsed,
			Solo: "False",
			HeadphonesVolumeF: "1",
			MixerCollapsed: "False",
			MixerVisible: "True",
			AudioDelay: "0",
			AudioChannel: "0",
			AudioGain: "1",
			AudioPad: "False",
			AudioCompressorEnabled: "False",
			AudioCompressorRatio: "1",
			AudioCompressorThreshold: "0.1258925",
			AudioNoiseGateEnabled: "False",
			AudioNoiseGateThreshold: "0",
			AudioEQEnabled: "False",
			AudioEQGainDB0: "0",
			AudioEQGainDB1: "0",
			AudioEQGainDB2: "0",
			AudioEQGainDB3: "0",
			AudioEQGainDB4: "0",
			AudioEQGainDB5: "0",
			AudioEQGainDB6: "0",
			AudioEQGainDB7: "0",
			AudioEQGainDB8: "0",
			AudioEQGainDB9: "0",
			AudioAGCEnabled: "True",
			AudioRackXML: {
				plugins: {},
			},
			...buses,
			FrameDelay: "0",
			Positions: {
				...XML_DECLARATION,
				ArrayOfMatrixPosition: {
					...XML_SCHEMA,
					MatrixPosition: [
						EMPTY_POSITION,
						EMPTY_WITH_MULTIPLY_POSITION,
						EMPTY_WITH_MULTIPLY_POSITION,
						EMPTY_WITH_MULTIPLY_POSITION,
						EMPTY_WITH_MULTIPLY_POSITION,
						EMPTY_WITH_MULTIPLY_POSITION,
					],
				},
			},
			PositionsExtended: {
				...XML_DECLARATION,
				ArrayOfMatrixPosition: {
					...XML_SCHEMA,
					MatrixPosition: [
						EMPTY_POSITION,
						EMPTY_WITH_MULTIPLY_POSITION,
						EMPTY_WITH_MULTIPLY_POSITION,
						EMPTY_WITH_MULTIPLY_POSITION,
						EMPTY_WITH_MULTIPLY_POSITION,
						EMPTY_WITH_MULTIPLY_POSITION,
						EMPTY_WITH_MULTIPLY_POSITION,
						EMPTY_WITH_MULTIPLY_POSITION,
						EMPTY_WITH_MULTIPLY_POSITION,
						EMPTY_WITH_MULTIPLY_POSITION,
						EMPTY_WITH_MULTIPLY_POSITION,
					],
				},
			},
			VirtualPTZPosition: {
				...XML_DECLARATION,
				MatrixPosition: {
					...XML_SCHEMA,
					...EMPTY_POSITION,
				},
			},
			Triggers: {
				...XML_DECLARATION,
				ArrayOfInputTrigger: {
					...XML_SCHEMA,
				},
			},
			TallyCOMPort: "None",
			TallyNumber: "0",
			AutoAudioMixing: "True",
			AutoPause: "True",
			AutoRestart: "True",
			AutoPlay: "True",
			Mirror: "False",
			SelectedIndex: "0",
			Rate: "1",
			...overlays,
			XML: "",
			ShaderSource: "00000000-0000-0000-0000-000000000000",
			...PTZ_OPTIONS,
			...(type === InputType.Video
				? {
						VideoInterlaced: "False",
				  }
				: {}),
			...(type === InputType.Color
				? {
						Colour: "0", // TODO add colours
						ColourBars: originalTitle === "Colour Bars" ? "True" : "False",
				  }
				: {}),
			...VIDEOSHADER_OPTIONS,
		},
		...(filePath ? { _text: filePath } : {}),
	};
};

export const buildVideoInput = (originalTitle: string, filePath: string) => {
	return buildInput(InputType.Video, originalTitle, filePath, "False", {
		BusMaster: "True",
	});
};

export const buildAudioInput = (originalTitle: string, filePath: string) => {
	return buildInput(InputType.Audio, originalTitle, filePath, "True", {
		BusMaster: "False",
		BusA: "True",
	});
};

export const buildColourBarsInput = () => {
	return buildInput(InputType.Color, "Colour Bars", null, "False", { BusMaster: "False" });
};

export const buildImageInput = (filePath: string, filename: string, collapsed: BoolType) => {
	return buildInput(InputType.Image, filename, filePath, collapsed, {}, []);
};
