import * as React from "react";
import { useRef, useState } from "react";
import { Preset } from "../../App";
import { getBlankPreset } from "../../features/file-loader/blankPreset";
import { Inputs } from "../../features/input/Input";
import { buildImageInput } from "../../features/input/inputs";
import { getInputKey } from "../../features/input/utils";
import { onOverlayInOverlayInput2In, onOverlayOutOverlayInput2Off } from "../../features/triggers/triggers";
import { downloadFile } from "../../util/file";
import { getPresetInputs, toXml } from "../../util/presetUtils";

import "./SlidesPresetBuilder.css";

const DEFAULT_MAIN_LANGUAGE = "RU";
const DEFAULT_OUTPUT_PRESET_FILE_NAME = "slides.vmix";

type SlideMaterial = {
	language: string;
	speechNumber: number;
	slideNumber: number;
	fullPath: string;
	filename: string;
};

const LOG = (message: string) => {
	console.log(message);
	alert(message);
};

const processMaterials = (
	preset: Preset,
	slides: SlideMaterial[]
): [updatedPreset: Preset, shiftedInputKeys: string[]] => {
	const inputs = preset.XML.Input;
	const shiftedInputKeys: string[] = [];

	let baseSlide: SlideMaterial = null;
	let baseSlideIndex = -1;

	inputs.forEach((input) => {
		input._attributes.Category = "1";
	});

	for (let i = 0; i < slides.length; i++) {
		const slide = slides[i];

		if (baseSlide !== null) {
			if (slide.speechNumber === baseSlide.speechNumber && slide.slideNumber === baseSlide.slideNumber) {
				const baseInput = inputs[baseSlideIndex];
				const overlayInput = inputs[i];

				baseInput._attributes.Triggers.ArrayOfInputTrigger.InputTrigger = [
					onOverlayInOverlayInput2In({
						input: overlayInput,
						inputIndex: i,
					}),
					onOverlayOutOverlayInput2Off(),
				];

				overlayInput._attributes.Category = "0";

				shiftedInputKeys.push(getInputKey(overlayInput));
			}
			baseSlide = slide;
			baseSlideIndex = i;
		} else {
			baseSlide = slide;
			baseSlideIndex = i;
		}
	}

	return [preset, shiftedInputKeys];
};

const SlidesPresetBuilder: React.FC = () => {
	const dropzoneRef = useRef(null);
	const [cnt, setCnt] = useState(0);
	const [mainLanguage, setMainLanguage] = useState(DEFAULT_MAIN_LANGUAGE);
	const [folderPath, setFolderPath] = useState("");
	const [slideMaterials, setSlideMaterials] = useState<SlideMaterial[]>([]);
	const [ignoreVmixPresetFiles, setIgnoreVmixPresetFiles] = useState(true);
	const [preset, setPreset] = useState<Preset>(null);
	const [shiftedInputKeys, setShiftedInputKeys] = useState<string[]>([]);

	const inputs = getPresetInputs(preset);

	const buildFilePath = (folderPath: string, filePath: string) => {
		return `${folderPath}\\${filePath.split("/").slice(2).join("\\")}`;
	};

	console.log("PRESET", preset);

	return (
		<div className="SlidesPresetBuilder">
			{preset === null && (
				<div>
					<label htmlFor="ignore-vmix-files">Ignore vMix preset files: </label>
					<input
						type="checkbox"
						id="ignore-vmix-files"
						checked={ignoreVmixPresetFiles}
						onChange={() => setIgnoreVmixPresetFiles(!ignoreVmixPresetFiles)}
					/>

					<br />
					<br />

					<div>1. Select folder with materials:</div>
					<br />
					<div
						ref={dropzoneRef}
						id="dropzone"
						onDragOver={(event) => {
							event.preventDefault();
						}}
						onDrop={async (event) => {
							event.preventDefault();
							let items = await getAllFileEntries(event.dataTransfer.items);

							const slides: SlideMaterial[] = items.map((item) => {
								if (item.name.match(/.+\.vmix$/)) {
									if (!ignoreVmixPresetFiles) {
										LOG(`Found vMix preset '${item.name}'. Will be ignored.`);
									}
									return null;
								}

								const filenameParts = item.name.match(/^([0-9]+)_([0-9]+)_(([a-zA-Z]{2,3})_)?.+\..+/);

								if (!filenameParts) {
									LOG(
										`File '${item.fullPath}' has invalid name format.\n\nExpected format:\n<speech_number>_<slide_number>_<language (optional)>_*`
									);
									return null;
								}

								const speechNumber = Number(filenameParts[1]);
								const slideNumber = Number(filenameParts[2]);
								const language = filenameParts[4] ? filenameParts[4].toUpperCase() : null;

								// validation
								const fullPathParts = item.fullPath.split("/").slice(1);
								const speechFolderName = fullPathParts[1];

								const speechFolderNameParts = speechFolderName.split("_");
								if (speechFolderNameParts.length < 2) {
									LOG(`Folder name '${speechFolderName}' has invalid format.\nExpected format: '00_Speaker name'.`);
									return null;
								}

								if (Number(speechFolderNameParts[0]) !== speechNumber) {
									LOG(
										`Looks like '${item.name}' is in wrong folder. Speech numbers '${speechFolderNameParts[0]}' and '${filenameParts[1]}' do not match.\nCurrent folder: '${speechFolderName}'.`
									);
								}

								return {
									fullPath: item.fullPath,
									filename: item.name,
									language,
									speechNumber,
									slideNumber,
								};
							});

							const filteredSlides = slides.filter((slide) => slide !== null);
							filteredSlides.sort((a, b) => {
								return Number(
									a.speechNumber - b.speechNumber || a.slideNumber - b.slideNumber || -(a.language === mainLanguage)
								);
							});

							console.log("ITEMS", items);
							console.log("SLIDES", filteredSlides);

							setSlideMaterials(filteredSlides);
						}}
					>
						{slideMaterials.length > 0 ? `✔️ ${slideMaterials.length} images loaded.` : "Drop folder or files here!"}
					</div>
					<br />
					<br />
					<div>2. Enter folder path:</div>
					<br />
					<input
						id="s"
						type="text"
						placeholder="Example: C:\..."
						value={folderPath}
						onChange={(event) => {
							setFolderPath(event.target.value);
						}}
					/>

					<br />
					<br />

					<button
						className="button-load-folder"
						style={{ marginLeft: "auto" }}
						disabled={!slideMaterials.length || !folderPath}
						onClick={() => {
							const blankPreset = getBlankPreset({ overlay1Cut: true });

							slideMaterials.forEach((slide) => {
								const { fullPath, filename, language } = slide;
								blankPreset.XML.Input.push(
									buildImageInput(
										buildFilePath(folderPath, fullPath),
										filename,
										language === mainLanguage || language === null ? "False" : "True"
									)
								);
							});

							setPreset(blankPreset);
						}}
					>
						Load images
					</button>
				</div>
			)}

			{preset && (
				<div>
					<div className="buttons">
						<button
							className="button-sort"
							onClick={() => {
								const [updatedPreset, shiftedInputKeys] = processMaterials(preset, slideMaterials);
								setPreset(updatedPreset);
								setShiftedInputKeys(shiftedInputKeys);
								setCnt(cnt + 1);
							}}
						>
							Process materials
						</button>

						<button
							className="button-download"
							style={{ marginLeft: "auto" }}
							onClick={() => {
								downloadFile(DEFAULT_OUTPUT_PRESET_FILE_NAME, toXml(preset));
							}}
						>
							Download preset
						</button>

						<button
							className="button-restart"
							onClick={() => {
								setMainLanguage(DEFAULT_MAIN_LANGUAGE);
								setFolderPath("");
								setSlideMaterials([]);
								setIgnoreVmixPresetFiles(true);
								setPreset(null);
								setShiftedInputKeys([]);
							}}
						>
							Restart
						</button>
					</div>

					<label htmlFor="main-language">Main language: </label>
					<input
						id="main-language"
						type="text"
						value={mainLanguage}
						onChange={(event) => setMainLanguage(event.target.value)}
					/>

					<br />
					<br />

					<Inputs inputs={inputs} shiftedInputKeys={shiftedInputKeys} onInputUpdated={() => {}} />
				</div>
			)}
		</div>
	);
};

// Drop handler function to get all files
async function getAllFileEntries(dataTransferItemList: DataTransferItemList): Promise<FileSystemFileEntry[]> {
	let fileEntries: FileSystemFileEntry[] = [];
	// Use BFS to traverse entire directory/file structure
	let queue: any[] = [];
	// Unfortunately dataTransferItemList is not iterable i.e. no forEach
	for (let i = 0; i < dataTransferItemList.length; i++) {
		queue.push(dataTransferItemList[i].webkitGetAsEntry());
	}
	while (queue.length > 0) {
		let entry = queue.shift();
		if (entry.isFile) {
			fileEntries.push(entry);
		} else if (entry.isDirectory) {
			let reader = entry.createReader();
			queue.push(...(await readAllDirectoryEntries(reader)));
		}
	}
	return fileEntries;
}

// Get all the entries (files or sub-directories) in a directory by calling readEntries until it returns empty array
async function readAllDirectoryEntries(directoryReader) {
	let entries = [];
	let readEntries = await readEntriesPromise(directoryReader);
	while (readEntries.length > 0) {
		entries.push(...readEntries);
		readEntries = await readEntriesPromise(directoryReader);
	}
	return entries;
}

// Wrap readEntries in a promise to make working with readEntries easier
async function readEntriesPromise(directoryReader): Promise<unknown[]> {
	try {
		return await new Promise((resolve, reject) => {
			directoryReader.readEntries(resolve, reject);
		});
	} catch (err) {
		console.log(err);
	}
}

export default SlidesPresetBuilder;
