import * as React from "react";
import { useRef, useState } from "react";
import { Preset } from "../../App";
import { buildAudioInput, buildVideoInput } from "../input/inputs";
import { Input, InputType } from "../input/types";
import { getBlankPreset } from "./blankPreset";

export type MaterialFile = {
	name: string;
	type: InputType;
};

type FolderLoaderProps = {
	onPresetLoaded: (preset: Preset) => void;
};

const parseFileAsMaterialFile = (file: File): MaterialFile => {
	let type = null;
	if (file.type.startsWith("video/")) {
		type = InputType.Video;
	}
	else if (file.type.startsWith("audio/")) {
		type = InputType.Audio;
	}

	return {
		name: file.name,
		type,
	};
};

const onFolderChange = (event: React.ChangeEvent<HTMLInputElement>): MaterialFile[] => {
	const folderFiles = event.target.files;
	return [...folderFiles].map(parseFileAsMaterialFile);
};

const buildInputFromMaterialFile = (materialFile: MaterialFile, folderPath: string): Input => {
	const { name, type } = materialFile;

	const filePath = `${folderPath}\\${materialFile.name}`;

	if (type === InputType.Audio) {
		return buildAudioInput(name, filePath);
	}
	else if (type === InputType.Video) {
		return buildVideoInput(name, filePath);
	}

	return null;
};

export const FolderLoader = (props: FolderLoaderProps) => {
	const fileInputRef = useRef(null);
	const [folderPath, setFolderPath] = useState("");
	const materialFiles = useRef<MaterialFile[]>([]);

	return (
		<div>
			<div>Select folder with materials:</div>
			<br />
			<input
				ref={fileInputRef}
				type="file"
				multiple
				onChange={async (event) => {
					materialFiles.current = onFolderChange(event);
				}}
			/>
			<br />
			<br />
			<div>Folder path:</div>
			<br />
			<input
				id="s"
				type="text"
				placeholder="Example: C:\..."
				value={folderPath}
				onChange={(event) => { setFolderPath(event.target.value) }}
			/>
			<button
				className="button-load-folder"
				style={{ marginLeft: "auto" }}
				disabled={!folderPath}
				onClick={() => {
					const blankPreset = getBlankPreset();

					materialFiles.current.forEach(materialFile => {
						blankPreset.XML.Input.push(buildInputFromMaterialFile(materialFile, folderPath));
					});

					props.onPresetLoaded(blankPreset);
				 }}
			>
				Load folder
			</button>
		</div>
	);
};
