import * as React from "react";
import { useState } from "react";
import { Preset } from "../../App";
import { parsePreset } from "../../util/presetUtils";

type FileLoaderProps = {
	onPresetLoaded: (filename: string, preset: Preset) => void;
	onMaterialsLoaded?: (files: string[]) => void;
};

const onFileChange = (event: React.ChangeEvent<HTMLInputElement>): Promise<{ filename: string; fileContent: string }> =>
	new Promise((resolve, reject) => {
		const presetFile = event.target.files[0];

		const reader = new FileReader();
		reader.readAsText(presetFile);

		const filename = presetFile.name;

		reader.onload = () => {
			resolve({ filename, fileContent: String(reader.result) });
		};

		reader.onerror = () => {
			reject({ filename, fileContent: reader.error });
		};
	});

export const FileLoader = (props: FileLoaderProps) => {
	const [loading, setLoading] = useState(false);

	return (
		<div>
			<div>Choose preset file:</div>
			<br />
			<input
				style={{ display: "block" }}
				type="file"
				onChange={async (event) => {
					setLoading(true);
					const { filename, fileContent } = await onFileChange(event);
					const preset = parsePreset(fileContent);
					setLoading(false);

					props.onPresetLoaded(filename, preset);
				}}
			/>
			<br />

			{loading && (
				<div>
					<br />
					<div>Loading...</div>
				</div>
			)}
		</div>
	);
};
