import * as React from "react";
import "buffer";

import "./App.css";
import { Input } from "./features/input/types";
import PresetPatcher from "./components/PresetPatcher/PresetPatcher";
import SlidesPresetBuilder from "./components/SlidesPresetBuilder/SlidesPresetBuilder";
import { State } from "./features/state/types";

import { HashRouter, NavLink, Route, Routes } from "react-router-dom";
import LanguagePresetBuilder from "./components/LanguagePresetBuilder/LanguagePresetBuilder";

export type Preset = {
	XML: {
		Input: Input[];
		State: State;
	};
};

export enum Page {
	PresetPatcher,
	LanguagePresetBuilder,
	SlidesPresetBuilder,
}

const App = () => {
	return (
		<div className="App">
			<HashRouter>
				<div className="menu-list">
					<NavLink to="/" className="menu-list-item">
						Preset Patcher
					</NavLink>
					<NavLink to="/languages" className="menu-list-item">
						Languages Preset Builder
					</NavLink>
					<NavLink to="/slides" className="menu-list-item">
						Slides Preset Builder
					</NavLink>
				</div>

				<div className="content">
					<Routes>
						<Route path="/" element={<PresetPatcher key={"preset-patcher"} />} />
						<Route path="/languages" element={<LanguagePresetBuilder key="language-preset-builder" />} />
						<Route path="/slides" element={<SlidesPresetBuilder key="slides-preset-builder" />} />
					</Routes>
				</div>
			</HashRouter>
		</div>
	);
};

export default App;
